@import url(https://fonts.googleapis.<link href="https://fonts.googleapis.com/css2?family=Nanum+Gothic&display=swap" rel="stylesheet">/css?family=Anton);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400&display=swap);
@import url(https://fonts.googleapis.<link href="https://fonts.googleapis.com/css2?family=Nanum+Gothic&display=swap" rel="stylesheet">/css?family=Anton);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.background {
  background-color: #000000;
}

.home-page {
  background-color: #000000;
}

.spotify-youtube-card {
  display: flex;
  width: 500px;
  height: 50%;
  margin: 0 auto;
  margin-top: 100px;
  border: none;
}

.spotify-youtube-card-img {
  width: 500px;
  height: 250px;
  border: none;
  padding: none;
  border-bottom-right-radius: 0px;
  border-bottom-left-radius: 0px;
}

.spotify-youtube-card-button {
  width: 500px;
  background-color: #1db954;
  border-color: #1db954;
  font-family: "Montserrat", sans-serif;
  border-top-right-radius: 0px;
  border-top-left-radius: 0px;
  padding: 5px;
}

.spotify-youtube-card-button:hover {
  opacity: 0.8;
  background-color: #ff0000;
  border-color: #ff0000;
  font-family: "Montserrat", sans-serif;
}

.spotify-youtube-card-button:active:after {
  background-color: #ff0000;
  border-color: #ff0000;
  font-family: "Montserrat", sans-serif;
}
.spotify-youtube-card-button:focus {
  opacity: 0.8;
  background-color: #ff0000;
  border-color: #ff0000;
  font-family: "Montserrat", sans-serif;
}

.App-navbar {
  background-color: #419c21;
  color: white;
  opacity: 0.5;
}

html {
  background-color: black;
}

* {
  background-color: black;
}

.playlist-page {
  display: flex;
  justify-content: center;
}

.hidden {
  background-color: black;
}

.pick-a-playlist-text {
  font-family: "Montserrat", sans-serif;
  font-size: 30px;
  color: white;
  animation: bounce;
  animation-duration: 2s;
  opacity: 0;
}

.playlists-container {
  margin-top: 50px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.playlist-button-and-image-container {
  margin-right: 30px;
  margin-bottom: 20px;
  justify-content: left;
  animation: fadeIn;
  animation-duration: 3s;
}

.playlist-button {
  display: flex;
  justify-content: center;
  width: 200px;
  height: 15px;
  text-align: center;
  align-items: center;
  font-size: 50%;
  font-family: "Montserrat", bold;
  background-color: #19720b;
  border-color: #19720b;
  line-height: 0.9;
  border-radius: 3px 3px 0px 0px;
}

.playlist-button:hover {
  opacity: 0.8;
  background-color: #ff0000;
  border-color: #ff0000;
}

.playlist-button:active {
  opacity: 0.2;
}

.playlist-image {
  width: 200px !important;
  height: 200px !important;
  border-radius: 0px 0px 4px 4px;
}

.playlist-image:hover {
  opacity: 0.8;
}

.navigation {
  top: 0;
  position: fixed;
  justify-content: left;
}

.navigation-back-button {
  margin-left: 0px;
  margin-top: 5px;
  display: flex;
  height: 20px;
  animation: fadeIn;
  animation-duration: 2s;
}

.button-container {
  width: 100%;
  display: flex;
  flex: wrap 1;
  margin-top: 2px;
  margin-bottom: 20px;
}
.youtube-back-button {
  margin-right: 30px;
}

.youtube-lyric-button {
  margin-right: 30px;
}

.youtube-lyric-button,
.spotify-youtube-previous-button,
.spotify-youtube-current-button,
.spotify-youtube-next-buttons,
.youtube-load-more-button {
  display: flex;
  justify-content: center;
  width: 140px;
  height: 30px;
  text-align: center;
  align-items: center;
  font-size: 50%;
  font-family: "Montserrat", bold;
  background-color: #19720b;
  border-color: #19720b;
  line-height: 0.9;
  border-radius: 3px 3px 0px 0px;
}

.spotify-youtube-previous-button {
  margin-right: 0px;
}

.spotify-youtube-current-button {
  background-color: red;
  border-color: red;
  margin-left: 0px;
}
.youtube-lyric-button:disabled,
.spotify-youtube-previous-button:disabled,
.spotify-youtube-current-button:disabled,
.spotify-youtube-next-buttons:disabled {
  background-color: grey;
  border-color: grey;
}

.player-and-sidebar {
  display: flex;
  justify-content: center;
}

#player {
  display: flex;
  align-items: center;
  margin-right: 20px;
}

.sidebar {
  border-color: white;
  display: flex;
  justify-content: center;
  margin-top: 100px;
}

.typed-and-spinner {
  border-color: white;
  justify-content: center;
}

.loading-typed {
  justify-content: center;
  color: green;
}

.loading-spinner {
  color: green;
  display: flex;
  justify-content: center;
  margin-left: 50%;
}

.modes-page {
  display: flex;
  justify-content: center;
  margin: 0 auto;
  margin-top: 200px;
}

.modes-container {
  display: flex;
  width: 1000px;
  justify-content: center;
  animation: fadeIn;
  animation-duration: 1.5s;
}

.dynamic-button-and-image-container {
  margin-right: 100px;
}

.playlist-image,
.dynamic-image {
  display: flex;
  justify-content: center;
  width: 200px;
  height: 200px;
  text-align: center;
  align-items: center;
  font-size: 50%;
  font-family: "Montserrat", bold;
  background-color: #19720b;
  border-color: #19720b;
  line-height: 0.9;
  border-radius: 3px 3px 0px 0px;
}

.playlist-button,
.dynamic-button {
  display: flex;
  justify-content: center;
  width: 200px;
  height: 15px;
  text-align: center;
  align-items: center;
  font-size: 50%;
  font-family: "Montserrat", bold;
  background-color: #19720b;
  border-color: #19720b;
  line-height: 0.9;
  border-radius: 3px 3px 0px 0px;
}

.dynamic-button:hover,
.playlist-button:hover {
  opacity: 0.8;
  background-color: #ff0000;
  border-color: #ff0000;
  font-family: "Montserrat", sans-serif;
}

.dynamic-image:hover,
.playlist-image:hover {
  opacity: 0.4;
}

.dynamic-lyric-button {
  height: 16px;
  margin-left: 50px;
  display: flex;
  justify-content: center;
  width: 140px;
  text-align: center;
  align-items: center;
  font-size: 50%;
  font-family: "Montserrat", bold;
  background-color: #19720b;
  border-color: #19720b;
  line-height: 0.9;
  border-radius: 3px 3px 0px 0px;
}

.dynamic-lyric-button:hover {
  background-color: red;
  border-color: red;
}

.invisible {
  background: transparent;
  border: none;
  outline: none;
  display: block;
  height: 20px;
  width: 20px;
  cursor: pointer;
}

.player-container {
  display: flex;
  flex: wrap 1;
  justify-content: center;
  align-items: center;
  align-content: center;
}

#player {
  margin-top: 50px;
}

